import React, { Component } from "react";
import styled, { ThemeProvider } from "styled-components";
import axios from "axios";
import { ToastContainer, toast } from "react-toastify";

const Button = styled.div`
  background-color: ${(props) => props.theme.badge.bg};
  border-color: ${(props) => props.theme.badge.bg};
  color: ${(props) => props.theme.badge.fg};

  &:hover {
    background-color: ${(props) => props.theme.badge.fg};
    border-color: ${(props) => props.theme.badge.fg};
    color: ${(props) => props.theme.badge.primaryText};
  }
`;

class ContactForm extends Component {
  state = {
    message: {
      subject: "",
      mobile: "",
      name: "",
      email: "",
      text: "",
    },
  };

  componentDidMount() {
    const message = { ...this.state.message };
    message.subject = this.props.subject;
    this.setState({ message });
  }

  handleChange = ({ currentTarget: input }) => {
    const message = { ...this.state.message };
    message[input.name] = input.value;
    this.setState({ message });
  };

  sendMessage = async () => {
    const options = {
      position: "bottom-center",
      autoClose: 5000,
      hideProgressBar: false,
      closeOnClick: true,
      pauseOnHover: true,
      draggable: true,
      progress: undefined,
    };

    try {
      await axios.post(
        "https://vinculobjetivobe.vercel.app/messages/send",
        this.state.message
      );

      toast.success("A sua mensagem foi enviada com sucesso!", options);
    } catch {
      toast.error(
        "Erro ao enviar mensagem. Por favor tente novamente mais tarde ou contacte-nos diretamente.",
        options
      );
    }
    const message = { ...this.state.message };
    message.name = "";
    message.mobile = "";
    message.email = "";
    message.text = "";

    this.setState({ message });
  };

  render() {
    const { message } = this.state;

    return (
      <ThemeProvider theme={this.props.theme}>
        <ToastContainer
          position="bottom-center"
          autoClose={5000}
          hideProgressBar={false}
          newestOnTop={false}
          closeOnClick
          rtl={false}
          pauseOnFocusLoss
          draggable
          pauseOnHover
        />
        <div className="row ml-lg-5 mr-lg-5 ml-sm-2 mr-sm-2 ml-1 mr-1">
          <div className="col-12 col-lg-9 mt-5">
            <h4>Tem alguma dúvida?</h4>
            <form>
              <div className="form-group">
                <div className="form-row">
                  <div className="col">
                    <label htmlFor="subject">Assunto</label>
                    <input
                      value={message.subject}
                      onChange={this.handleChange}
                      type="text"
                      name="subject"
                      id="subject"
                      className="form-control"
                    />
                  </div>
                  <div className="col">
                    <label htmlFor="mobile">Contacto</label>
                    <input
                      value={message.mobile}
                      onChange={this.handleChange}
                      placeholder="Contacto"
                      type="text"
                      name="mobile"
                      id="mobile"
                      className="form-control"
                    />
                  </div>
                </div>
              </div>

              <div className="form-row">
                <div className="form-group col-md-6">
                  <label htmlFor="name">Nome</label>
                  <input
                    value={message.name}
                    onChange={this.handleChange}
                    type="text"
                    className="form-control"
                    name="name"
                    placeholder="Nome"
                  />
                </div>
                <div className="form-group col-md-6">
                  <label htmlFor="email">Email</label>
                  <input
                    value={message.email}
                    onChange={this.handleChange}
                    type="email"
                    className="form-control"
                    name="email"
                    placeholder="Email"
                  />
                </div>
              </div>

              <div className="form-row">
                <div className="form-group col-12">
                  <label htmlFor="text">Mensagem</label>
                  <textarea
                    value={message.text}
                    onChange={this.handleChange}
                    type="text"
                    className="form-control"
                    name="text"
                    placeholder="Pedido de informações"
                  ></textarea>
                </div>
              </div>

              <div className="form-group"></div>
              <Button
                className="btn btn-primary mb-5"
                onClick={this.sendMessage}
              >
                Enviar
              </Button>
              {this.state.messageStatus && (
                <small>{this.state.messageStatus}</small>
              )}
            </form>
          </div>
        </div>
      </ThemeProvider>
    );
  }
}

export default ContactForm;
