import React, { Component } from "react";
import { cars } from "../listOfCars";
import styled, { ThemeProvider } from "styled-components";

const FilterTitle = styled.h4`
  margin: 15px 0px;
  color: ${(props) => props.theme.primaryText};
`;

const Button = styled.button`
  background-color: ${(props) => props.theme.buttonBgColor};
  color: ${(props) => props.theme.buttonFgColor};

  &.active {
    background-color: ${(props) => props.theme.activeBgColor};
    color: ${(props) => props.theme.activeFgColor};
    border-color: ${(props) => props.theme.activeBgColor};
  }

  & span {
    background-color: ${(props) => props.theme.badgeBgColor};
    color: ${(props) => props.theme.badgeFgColor};
    margin-left: 10px;
  }
`;

class Filter extends Component {
  state = {};

  getUniqueBrands() {
    let brands = [];
    for (let car of cars) {
      let b = car.attributes.brand.value;
      if (!brands.includes(b)) {
        brands.push(b);
      }
    }
    return brands;
  }

  countBrand(brand) {
    return cars.filter((c) => c.attributes.brand.value === brand).length;
  }

  countSold() {
    return cars.filter((c) => c.sold).length;
  }

  render() {
    const { showSold, selectedBrands } = this.props;

    return (
      <ThemeProvider theme={this.props.theme}>
        <FilterTitle className="title1">Filtros</FilterTitle>

        <div className="list-group filter1">
          <Button
            className={`list-group-item d-flex justify-content-between align-items-center ${
              showSold ? "active" : ""
            }`}
            onClick={() => this.props.onDisableAvailableFilter()}
          >
            Todos
            <span className="badge badge-primary badge-pill">
              {cars.length}
            </span>
          </Button>

          <Button
            className={`list-group-item d-flex justify-content-between align-items-center ${
              !showSold ? "active" : ""
            }`}
            onClick={() => this.props.onClickAvailable()}
          >
            Disponíveis
            <span className="badge badge-primary badge-pill">
              {cars.length - this.countSold()}
            </span>
          </Button>
        </div>

        <FilterTitle className="title2">Marcas</FilterTitle>

        <div className="list-group filter2">
          <Button
            className={`list-group-item d-flex justify-content-between align-items-center ${
              selectedBrands.length === 0 ? "active" : ""
            }`}
            onClick={() => this.props.onClickBrand("all")}
          >
            Todas
            <span className="badge badge-primary badge-pill">
              {cars.length}
            </span>
          </Button>
          {this.getUniqueBrands().map((brand) => {
            return (
              <Button
                className={`list-group-item d-flex justify-content-between align-items-center ${
                  selectedBrands.includes(brand) ? "active" : ""
                }`}
                onClick={() => this.props.onClickBrand(brand)}
              >
                {brand}
                <span className="badge badge-primary badge-pill">
                  {this.countBrand(brand)}
                </span>
              </Button>
            );
          })}
        </div>
      </ThemeProvider>
    );
  }
}

export default Filter;
