import React, { Component } from "react";
import { Splide, SplideSlide } from "@splidejs/react-splide";
import styled, { ThemeProvider } from "styled-components";
import axios from "axios";
import LoadingSpinner, { LoadingContainer } from "./common/loading";
import ContactForm from "./common/contact-form";

const SliderContainer = styled.div`
  & img {
    width: inherit;
    background-image: url("/img/pulse.gif");
    background-repeat: no-repeat;
    background-position: center;
  }
`;

const DetailsList = styled.ul`
  list-style-type: none;
  padding-left: 0px;
  color: ${(props) => props.theme.primaryText};
`;

const H1 = styled.h1`
  color: ${(props) => props.theme.primaryText};
`;

const Error404 = styled.div`
  margin: 50px auto;
  color: ${(props) => props.theme.bgColor};
  -webkit-filter: invert(100%);
  filter: invert(100%);
  font-size: 40px;
  text-align: center;

  & span {
    font-size: 1.4em;
    border-bottom: 3px solid red;
  }
`;

class CarDetails extends Component {
  state = {};

  constructor(props) {
    super(props);

    this.primaryRef = React.createRef();
    this.secundaryRef = React.createRef();
  }

  renderSlides(car) {
    const items = [];

    for (let i = 1; i <= car.photos; i++) {
      items.push(
        <SplideSlide key={`${car.id}/${i}`}>
          <img
            src={process.env.PUBLIC_URL + `/img/${car.id}/${i}.png`}
            alt={"Fotos da viatura"}
          />
        </SplideSlide>
      );
    }

    return items;
  }

  componentDidUpdate(prevProps, prevState) {
    if (this.primaryRef.current !== null) {
      this.primaryRef.current.sync(this.secundaryRef.current.splide);
    }
  }

  async componentDidMount() {
    // Set the sync target right after the component is mounted.
    if (this.primaryRef.current !== null) {
      this.primaryRef.current.sync(this.secundaryRef.current.splide);
    }

    const { data: cars } = await axios.get(
      "https://vinculobjetivobe.vercel.app/cars"
    );

    if (cars) {
      const car = cars.find(
        (c) => c.id.toString() === this.props.match.params.id
      );
      document.title = car.title;
    }

    this.setState({ cars });
  }

  GetCarSpecs(car) {
    return Object.keys(car.attributes).map((key) => {
      return (
        <li key={key}>
          <strong>{car.attributes[key].description}:</strong>{" "}
          {car.attributes[key].value}
        </li>
      );
    });
  }

  render() {
    const primaryOptions = {
      type: "loop",
      perPage: 1,
      perMove: 1,
      gap: "1rem",
      pagination: true,
      lazyLoad: "nearby",
    };

    const secondaryOptions = {
      type: "slide",
      rewind: true,
      gap: "1rem",
      pagination: false,
      fixedWidth: 110,
      fixedHeight: 70,
      cover: true,
      focus: "center",
      isNavigation: true,
      updateOnMove: true,
    };

    const { cars } = this.state;

    if (!cars) {
      return (
        <LoadingContainer>
          <LoadingSpinner></LoadingSpinner>
        </LoadingContainer>
      );
    }

    const { id } = this.props.match.params;
    const car = cars.find((c) => c.id.toString() === id);

    if (id > cars.length) {
      return (
        <ThemeProvider theme={this.props.theme}>
          <Error404>
            <span>Erro:</span> ID da viatura não existe: {id}
          </Error404>
        </ThemeProvider>
      );
    }

    return (
      <ThemeProvider theme={this.props.theme}>
        <div className="row ml-lg-5 mr-lg-5 ml-sm-2 mr-sm-2 ml-1 mr-1">
          <div className="col-12 col-lg-9 mt-5">
            <SliderContainer>
              <Splide options={primaryOptions} ref={this.primaryRef}>
                {this.renderSlides(car)}
              </Splide>
              <Splide
                options={secondaryOptions}
                ref={this.secundaryRef}
                className="mt-3"
              >
                {this.renderSlides(car)}
              </Splide>
            </SliderContainer>
          </div>
          <div className="col-12 col-lg-3 mt-5">
            <H1>{car.title}</H1>
            <DetailsList>{this.GetCarSpecs(car)}</DetailsList>
          </div>
        </div>

        <ContactForm theme={this.props.theme} subject={car.title}></ContactForm>
      </ThemeProvider>
    );
  }
}

export default CarDetails;
